* {
  box-sizing: border-box;
  --text-color: #00acdc;
}

body {
  background-color: gray;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-width: 1024px;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  align-items: center;
  display: flex;
}

.nav_list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_item:not(:last-child) {
  margin: 0 20px 0 0;
}

.gadgets {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.apple, .gadgets:hover {
  color: var(--text-color);
}

.apple:after {
  content: "";
  background-color: var(--text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
